.banner {
  text-align: center;
}

.content {
  margin: 0 5%;
}

.loader {
  text-align: center;
}

.generations {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.pokemon-list {
  display: grid;
  gap: 1rem;

  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
}

.pokemon-list > .pokemon-card {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: #272727;
  border: 2px solid #747474;
}
